.main-page-about-restaurant {
  background-color: var(--first-color-fon);
  position: relative;
}
.main-page-photo-about-restaurant {
  // position: absolute;
  // top: 210px;
  // right: 15px;
  width: 65%;
  margin-top: 28px;
  margin-left: auto;

  @include mobile {
    width: 246px;
  }
  @include tablet {
    top: -32px;
    right: 112px;
  }
  @include desktop {
    right: 180px;
  }
}
.main-page-second-photo-about-restaurant {
  margin-top: 32px;
  // margin-bottom: 349px;
  display: flex;
  align-items: center;
  gap: 20px;

  @include tablet {
    margin-top: 142px;
    gap: 48px;
  }
  @include desktop {
    margin-top: 162px;
    gap: 40px;
  }
  & picture {
    width: 50%;
    @include mobile {
      max-width: 200px;
    }
  }
  & p {
    width: 41%;
  }
}
.main-page-second-photo-about-restaurant-text {
  font-size: 12px;
  line-height: 1.25;
  line-height: 100%;
  letter-spacing: 0.02em;
  color: var(--second-accent-color);
  width: 163px;

  @include tablet {
    font-size: 16px;
    width: 312px;
  }
}

