  --second-text-color: #bababa;
  --pre-title-text-color: #b4b4b4;
  --text-menu-color: #000;
  --white-color: #fff;
  --placeholder-color: gray;
  --shadow: 5px 5px 70px 17px rgba(0, 0, 0, .25);
  --gradient: rgba(0, 0, 0, .65);
  --animation-link: 250ms cubic-bezier(.4, 0, .2, 1);
